export default {
	data() {
		return {
			img:[],
			list:'',
			pagemax:'',//总的数量
			pagenow:0,//当前的页数
			shownumber:12,//当前页面的条数
			pagenumber:12,//每页的分页条数
			keywords:'',
			contentText: {
				contentdown: "查看更多",
				contentrefresh: "加载中",
				contentnomore: "没有更多"
			},
			status:'loading',
			shouLoading:true,
			haveData:false,
		}
	},
	onLoad(e) {
		var t=this;	
		t.img[0]=t.sta.sitestatic+'img/dl-icon.png';
		//获取新闻列表
		t.getDangList();
	},
	onUnload() {
		this.max = 0,
		this.data = [],
		this.loadMoreText = "加载更多",
		this.showLoadMore = false;
	},
	onReachBottom() {
		if (this.shownumber >= this.pagemax) {
			return;
		}
		setTimeout(() => {
			this.setDate();
		}, 100);
	},
	onPullDownRefresh() {
		this.initData();
	},	
	methods: {
		search:function(){
			uni.showLoading();
			var t=this;
			var data={
				party_no:t.globalData.userInfo.party_no,
				keywords:t.keywords,
				pagesize:999,
				page:0,
			}
			t.urlRequest.urlRequest(t.func.getDangList,data,function(res){
				var data=t.dealData(res);
				t.list=data;
				uni.hideLoading();
			})
		},
		getDangList:function(){
			uni.showLoading();
			var t=this;
			
			var data={
				party_no:t.globalData.userInfo.party_no,
				pagesize:t.shownumber,
				page:t.pagenow,
			}
			t.urlRequest.urlRequest(t.func.getDangList,data,function(res){
				var data=t.dealData(res);
				t.list=data;
				uni.hideLoading();
			})
			
		},
		initData(){
			uni.showLoading();
			const t = this;
			t.shownumber=12;
			t.pagenow=0;
			var data={
				party_no:t.globalData.userInfo.party_no,
				pagesize:t.shownumber,
				page:t.pagenow,
			}
			t.urlRequest.urlRequest(t.func.getDangList,data,function(res){
				var data=t.dealData(res);
				t.list=data;
				uni.hideLoading();
				uni.stopPullDownRefresh();
			})
		},
		setDate() {
			uni.showLoading();
			this.shownumber=this.pagenow*this.pagenumber;
			this.pagenow++;
			var t=this;
			
			var data={
				party_no:t.globalData.userInfo.party_no,
				page:t.pagenow,
				pagesize:t.pagenumber
			}
			t.urlRequest.urlRequest(t.func.getDangList,data,function(res){
				if(res.data.list){
					setTimeout(() => {
						t.haveData =true
					}, 100);
					var data=t.dealData(res);
					t.list = t.list.concat(data);
					
				}
				
				uni.hideLoading();
			})
			
		},
		dealData:function(res){
			var t=this;
			t.pagemax=res.data.count
			if(res.data.list){
				
				for (var i = 0; i < res.data.list.length; i++) {
					if(res.data.list[i]['communist_avatar']){
						res.data.list[i]['img']=t.sta.siteupload+res.data.list[i]['communist_avatar'];
					}else{
						res.data.list[i]['img']=t.sta.sitestatic+'img/oa_img_head.png';
					}
				}
			}
			return res.data.list
		}
	}
}
